import Web3 from 'web3';

const url = 'https://sandbox.truffleteams.com/8f7572d1-e253-420a-93bc-2ed8a6f051e6';
const web3 = new Web3(url);
const from = '0x61eB15d8A761Fc80387F50d84Fbf7Ff47a97d92F';

const originalAddress = '0x39Da4827682697868867a85ecEB083eF1B620FC0';
const originalAbi = require('./originalAbi.json');
const original = new web3.eth.Contract(originalAbi, originalAddress);

const wrappedAddress = '0xc109EAb96c6897D51B272ED09a7e4C42e2AfDB77';
const wrappedAbi = require('./wrappedAbi.json');
const wrapped = new web3.eth.Contract(wrappedAbi, wrappedAddress);

async function main() {
    const accounts = await web3.eth.getAccounts();
    const from = accounts[0];



    await original.methods.setWrapped(wrappedAddress).send({ from });

    const name = await original.methods.wrapped().call({ from });
    console.log(name);

    const value = 1;
    await original.methods.deposit().send({ from, value });
}

function setWrapped(wrappedAddress) {
    original.methods.setWrapped(wrappedAddress).send({ from });
}

async function deposit(value) {
    const hash = await original.methods.deposit().send({ from, value });
    return hash;
}

async function getAccounts() {
    const accounts = await web3.eth.getAccounts();
    return accounts
}

async function getBalance() {
    const balance = await wrapped.methods.balances(from).call({ from });
    return balance;
}

export { original, getAccounts, deposit, getBalance };

