import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Paper, Grid, Toolbar, Typography, TextField, MenuItem, Button, Tooltip, Divider, Box } from '@material-ui/core';

import * as contract from './contract/contracts';
import Inline from './Inline';

const useStyles = makeStyles((theme) => ({
    root: {
        // flexGrow: 1,
        backgroundColor: theme.palette.secondary.main,
        padding: theme.spacing(9),
        
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '99%',
        },
    },
    paper: {
        padding: theme.spacing(2),
        height: '99%',
        textAlign: 'center',
    },
}));

const originals = ['Ethereum', 'Avalanche', 'Binance', 'Counterparty'];
const wraps = ['Ethereum', 'Avalanche', 'Binance', 'Counterparty'];

export default function CenteredGrid() {
    const classes = useStyles();

    const [original, setoriginal] = React.useState(originals[0]);
    const [wrapped, setwrapped] = React.useState(wraps[0]);
    const [value, setvalue] = React.useState(5);
    const [accounts, setaccounts] = React.useState([]);
    const [balance, setbalance] = React.useState(0);
    const [to, setto] = React.useState(accounts[0]);

    React.useEffect(() => {
        contract.getAccounts()
            .then(x => setaccounts(x));

        contract.getBalance()
            .then(x => setbalance(x));
    }, [accounts, balance])

    const hoverText = (
        <Typography>
            {`Create ${value} wrapped ${original} tokens 
            on the ${wrapped} network`}
        </Typography>
    );

    async function onClick() {
        const hash = await contract.deposit(value);
        console.log(hash);
    }

    function AddressInput() {
        const items = accounts.map(value => (
            <MenuItem
                value={value}
            >
                {value}
            </MenuItem>
        ));

        return (
            <TextField
                select
                label='To Address'
                value={to}
                onChange={event => {
                    setto(event.target.value)
                }}
            >
                {items}
            </TextField>

        );
    }

    function NumberInput() {
        return (
            <TextField
                type='number'
                label='Token Amount'
                value={value}
                onChange={event => {
                    setvalue(event.target.value)
                }}
            />
        );
    }

    return (
        <div className={classes.root}>
            <Grid container
                spacing={3}
                // justifyContent='center'
            >
                <Grid item xs>
                    <Paper className={classes.paper}>
                        <Typography variant='h6'>
                            Original Token
                        </Typography>

                        <TextField
                            select
                            label='Original Network'
                            value={original}
                            onChange={event => {
                                setoriginal(event.target.value)
                            }}
                        >
                            {originals.map(value => (
                                <MenuItem
                                    value={value}
                                >
                                    {value}
                                </MenuItem>
                            ))}
                        </TextField>

                        <NumberInput />
                    </Paper>
                </Grid>
                
                <Grid item xs>
                    <Paper className={classes.paper}>
                        
                        <Typography variant='h6'>
                            Wrapped Token
                        </Typography>

                        <TextField
                            select
                            label='Wrapped Network'
                            value={wrapped}
                            onChange={event => {
                                setwrapped(event.target.value)
                            }}
                        >
                            {wraps.map(value => (
                                <MenuItem
                                    value={value}
                                >
                                    {value}
                                </MenuItem>
                            ))}
                        </TextField>

                        <br /><br />
                        
                        <Tooltip
                            title={hoverText}
                        >
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={onClick}
                            >
                                Submit
                            </Button>
                        </Tooltip>
                    </Paper>
                </Grid>

                <Grid item xs>
                    <Paper className={classes.paper}>
                        <Typography>
                            Current Balance: {balance}
                        </Typography>
                    </Paper>
                </Grid>

                <Grid item xs>
                    <Paper className={classes.paper}>
                        
                        <Typography variant='h6'>
                            Transfer Wrapped Tokens
                        </Typography>

                        {/* <AddressInput /> */}
                        <NumberInput />
                        
                        <Tooltip
                            title={hoverText}
                        >
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={onClick}
                            >
                                Submit
                            </Button>
                        </Tooltip>
                    </Paper>
                </Grid>

            </Grid>

        </div>
    );
}
